<template>
  <div class="one-fifth-video-col">
   <Form
      ref="addForm"
      :model="addForm"
      :rules="addRules"
      :label-width="85"
    >
      <span>
        <h3>!
         <a
          href="javascript:void(0)"
          style="font-size:14px;margin-left:2%;"
          @click="jumper('https://xly.bce.baidu.com/paddlepaddle/PR-Location/newipipe/builds/23574?module=github/PaddlePaddle/Paddle&pipeline=PR-Location&branch=branches')"
        >  </a>
      </h3>
      </span>
      <Row style="margin-top:2%;">
        <Col span="4">
          <FormItem label="repo:" prop="repo_name">
            <Select clearable v-model="addForm.repo_name">
              <Option
                :key="index"
                :value="item"
                v-for="(item, index) in Repos"
              >{{ item }}</Option>
            </Select>
          </FormItem>
        </Col>
        <Col span="10">
          <FormItem label="case:" prop="model_name">
            <Input v-model="addForm.model_name" placeholder="case"/>
          </FormItem>
        </Col>
        <Col span="10">
          <FormItem label=":" prop="step_name">
            <Input v-model="addForm.step_name" placeholder=""/>
          </FormItem>
        </Col>
      </Row>
      <Row>
        <Col span="4">
          <FormItem label=":" prop="problem_type">
            <Select clearable v-model="addForm.problem_type">
              <Option
                :key="index"
                :value="item.key"
                v-for="(item, index) in problemType"
              >{{ item.desc }}</Option>
            </Select>
          </FormItem>
        </Col>
        <Col span="10">
          <FormItem label=":" prop="search_days">
            <Input v-model="addForm.search_days" placeholder=""/>
          </FormItem>
        </Col>
        <Col span="10">
          <FormItem label=":" prop="key_word">
            <Input v-model="addForm.key_word" placeholder=""/>
          </FormItem>
        </Col>
      </Row>
      <Row>
        <Col span="4">
          <FormItem label=":" prop="baseline">
            <Input v-model="addForm.baseline" placeholder=""/>
          </FormItem>
        </Col>
        <Col span="10">
          <FormItem label=":" prop="threshold">
            <Input v-model="addForm.threshold" placeholder=""/>
          </FormItem>
        </Col>
        <Col span="10">
          <FormItem label=":" prop="email_add">
            <Input v-model="addForm.email_add" placeholder=""/>
          </FormItem>
        </Col>
      </Row>
      <Row justify="center">
        <Button class="btn-success" @click="handleSummit"></Button>
      </Row>
    </Form>
    <Modal
      width="700px"
      v-model="show"
      title=""
      v-on:on-cancel="handleClose"
    >
      <div>
        <a
          href="javascript:void(0)"
          style="font-size:14px;margin-left:2%;"
          @click="jumper(joburl)"
        > {{ joburl }} </a>
      </div>
      <div slot="footer">
        <Button type="primary" @click="handleClose"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { BinarySearchUrl } from '../api/url.js';
import api from '../api/index';

export default {
  name: 'binarySearch',
  data: function () {
    return {
      joburl: '',
      show: false,
      Repos: [
        'Paddle',
        'PaddleOCR',
        'PaddleNLP',
        'PaddleClas',
        'PaddleGAN',
        'PaddleSlim',
        'PaddleRec',
        'PaddleDetection',
        'PaddleSeg',
        'PaddleSpeech'
      ],
      problemType: [
        {
          key: '1',
          desc: ''
        },
        {
          key: '2',
          desc: ''
        },
        {
          key: '3',
          desc: ''
        },
        {
          key: '4',
          desc: ''
        }
      ],
      addForm: {
        repo_name: '',
        model_name: '',
        step_name: '',
        problem_type: '',
        search_days: '',
        key_word: '',
        threshold: '',
        email_add: ''
      },
      addRules: {
        repo_name: [
          { required: true, message: 'repo', trigger: 'blur' }
        ],
        model_name: [
          { required: true, message: 'case', trigger: 'blur' }
        ],
        problem_type: [
          { required: true, message: '', trigger: 'blur' }
        ],
        search_days: [
          { required: true, message: '', trigger: 'blur' }
        ],
        email_add: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    };
  },
  watch: {
  },
  mounted: function () {
    this.initData();
  },
  components: {
  },
  computed: {
  },
  methods: {
    handleSummit() {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          this.createJob();
        } else {
          this.$Message.error('');
        }
      });
    },
    jumper(url) {
      window.open(url, '_blank');
    },
    initData() {
      this.addForm = {
        repo_name: '',
        model_name: '',
        step_name: '',
        problem_type: '',
        search_days: '',
        key_word: '',
        threshold: '',
        email_add: ''
      };
      this.joburl = '';
      this.show = false;
    },
    handleClose() {
      this.initData();
    },
    async createJob() {
      const {code, data, message} = await api.post(BinarySearchUrl, this.addForm);
      if (parseInt(code, 10) === 200) {
        this.joburl = data.url;
        this.show = true;
      } else {
        this.$Message.error({
          content: ': ' + message,
          duration: 30,
          closable: true
        });
      }
    }
  }
};
</script>

<style scoped>
.one-fifth-video-col {
  margin-right: 1%;
  margin-left: 1%;
  margin-bottom: 1%;
  margin-top: 1%;
  font-size:14px;
}
.btn-success {
  color: #fff;
  background-color: #67c23a;
  border-color: #67c23a;
}
</style>



