<template>
  <div>
    <div>
      <Form
        :model="search"
        :label-width="75"
      >
        <Row>
          <Col span="12">
            <FormItem label=":" prop="dt" class="form-css">
              <DatePicker
                type="daterange"
                placement="bottom-end"
                placeholder="    "
                v-model="search.dt"
                style="width:80%"
                v-on:on-change="searchByfilters"
              ></DatePicker>
            </FormItem>
          </Col>
        </Row>
        <div style="margin-top: 1%;">
          <Row>
            <Col span="6">
              <FormItem label=":" prop="keyword" class="form-css">
                <Input
                  clearable
                  v-model="search.keyword"
                  placeholder=""/>
              </FormItem>
            </Col>
            <Col span="6" v-if="userInfo.identifyQA">
              <FormItem label="RD:" prop="rdname" class="form-css">
                <Input
                  clearable
                  v-model="search.rdname"
                  placeholder="RD"/>
              </FormItem>
            </Col>
            <Col span="6" v-else>
              <FormItem label="QA:" prop="qaname" class="form-css">
                <Input
                  clearable
                  v-model="search.qaname"
                  placeholder="QA"/>
              </FormItem>
            </Col>
            <Col span="4">
              <FormItem label=":" prop="staus" class="form-css">
                <Select
                  clearable
                  filterable
                  v-model="search.status"
                  v-on:on-change="searchByfilters"
                >
                  <Option
                    :key="index"
                    :value="item"
                    v-for="(item, index) in statusList"
                  >{{ item }}</Option>
                </Select>
              </FormItem>
            </Col>
            <Col span="2" offset="1">
              <Button
                class="btn-success"
                shape="circle"
                icon="ios-search"
                @click="searchByfilters"
              >Search</Button>
            </Col>
            <Col span="2" offset="1">
              <Button
                icon="md-add"
                class="btn-success"
                @click="showCreateModa"
              ></Button>
            </Col>
          </Row>
        </div>
      </Form>
    </div>
    <div>
      <!--
      <div style="text-align: right;">
        <Button
          icon="md-add"
          class="btn-success"
          @click="showCreateModa"
        ></Button>
      </div>
      -->
      <div style="margin-top: 1%;">
        <Table
          :columns="columns"
          :data="content"
        ></Table>
        <Page
          :total="total"
          :current="parseInt(page)"
          :page-size="parseInt(pagesize)"
          size="small"
          style="text-align: center;"
          v-on:on-change="pageChange"
          >
        </Page>
      </div>
    </div>
    <Modal
      v-model="showModa"
      title=""
      width="60%"
      v-on:on-cancel="handleReset"
    >
      <test-job
        ref="child"
        @closeModal="closeModal"
        @searchByfilter="searchByfilter"
        @SubmitRequirement="SubmitRequirement"
      ></test-job>
      <div slot="footer">
        <Button type="text" @click="handleReset"></Button>
        <Button type="primary" @click="handleSubmit"></Button>
      </div>
    </Modal>
    <Modal
      v-model="createTestModa"
      title=""
      width="40%"
      v-on:on-cancel="handleResetTest"
    >
      <Form
        ref="reqDetail"
        :model="reqDetail"
        :rules="addReqRules"
        :label-width="75"
        style="width: 90%"
      >
        <FormItem label="icafeID:" prop="rd">
          <Input
            disabled
            v-model="reqDetail.icafe_id"
            placeholder="icafeId"
          />
        </FormItem>
        <FormItem label="RD:" prop="rd">
          <Input
            v-model="reqDetail.rd"
            placeholder="RD"
          />
        </FormItem>
        <FormItem label="QA:" prop="qa">
          <Input v-model="reqDetail.qa" placeholder="QA"/>
        </FormItem>
        <FormItem label="repo:" prop="repo">
          <Input v-model="reqDetail.repo" placeholder="repo"/>
        </FormItem>
        <!--
        <FormItem label=":" prop="branch">
          <Input v-model="reqDetail.branch" placeholder=""/>
        </FormItem>
        -->
        <FormItem label="pr:" prop="pr">
          <Input v-model="reqDetail.pr" placeholder="pr"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="handleResetTest"></Button>
        <Button type="primary" @click="handleCreateTest"></Button>
      </div>
    </Modal>
    <Modal
      v-model="createReqModa"
      title=""
      width="40%"
      v-on:on-cancel="handleResetReq"
    >
      <Form
        ref="addReqForm"
        :model="addReqForm"
        :label-width="75"
        :rules="addRules"
        style="width: 90%"
      >
        <FormItem label=":" prop="type">
          <Select
            clearable
            filterable
            v-model="addReqForm.type"
          >
            <Option
              :key="index"
              :value="item"
              v-for="(item, index) in bugType"
            >{{ item }}</Option>
          </Select>
        </FormItem>
        <FormItem label=":" prop="title">
          <Input v-model="addReqForm.title" placeholder=""/>
        </FormItem>
        <FormItem label=":" prop="detail">
          <Input
            clearable
            v-model="addReqForm.detail"
            type="textarea"
            placeholder=""
            :autosize="{minRows: 2,maxRows: 30}"
          />
        </FormItem>
        <FormItem label="QA:" prop="qa_owner">
          <Input
            clearable
            v-model="addReqForm.qa_owner"
            placeholder="QA"
          ></Input>
        </FormItem>
        <FormItem label="RD:" prop="rd_owner">
          <Input
            clearable
            v-model="addReqForm.rd_owner"
            placeholder="RD"
          ></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="handleResetReq"></Button>
        <Button type="primary" @click="handleCreateReq"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import Cookies from 'js-cookie';
import { dateFmt } from '../../util/help.js';
import { RequirementSearchUrl, UserCheckUrl, StartTestUrl, CreateReqUrl } from '../../api/url.js';
import api from '../../api/index';
import TestJob from '../PTS/TestJob.vue';
import baseChild from './baseChild.vue';

export default {
  name: 'Personal',
  data: function () {
    return {
      addReqForm: {
        type: 'Task',
        rd_owner: '',
        qa_owner: '',
        title: '',
        detail: ''
      },
      addRules: {
        type: [
          { required: true, message: '', trigger: 'blur' }
        ],
        title: [
          { required: true, message: '', trigger: 'blur' }
        ],
        qa_owner: [
          { required: true, message: 'QA', trigger: 'blur' }
        ],
        rd_owner: [
          { required: true, message: 'RD', trigger: 'blur' }
        ]
      },
      addReqRules: {
        qa: [
          { required: true, message: 'QA', trigger: 'blur' }
        ],
        rd: [
          { required: true, message: 'RD', trigger: 'blur' }
        ],
        repo: [
          { required: true, message: 'Repo', trigger: 'blur' }
        ],
        pr: [
          { required: true, message: 'pr', trigger: 'blur' }
        ]
      },
      reqDetail: {
        method: '',
        rd: '',
        qa: '',
        pr: '',
        branch: '',
        repo: '',
        icafe_id: ''
      },
      createTestModa: false,
      createReqModa: false,
      selectRow: null,
      showModa: false,
      total: 0,
      page: 1,
      pagesize: 15,
      userInfo: {
        identifyQA: false,
        username: Cookies.get('username')
      },
      bugType: [
        'Task'
      ],
      statusList: [
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      content: [
      ],
      search: {
        keyword: '',
        qaname: '',
        rdname: '',
        status: '',
        dt: [this.getBeginData(), new Date()]
      },
      columns: [
        {
          title: 'IcafeID',
          key: 'sequence',
          align: 'center',
          fixed: 'left',
          minWidth: 100,
          render: (h, params) => {
            let ret = [];
            if (params.row.child.length > 0) {
              ret.push(
                h('Tooltip', {
                  props: {
                    placement: 'top',
                    transfer: true
                  }
                }, [
                  h('div', [
                    h('a', {
                      href: 'javascript:void(0);',
                      style: {
                        marginRight: '5px'
                      },
                      on: {
                        click: () => {
                          this.jumper(params.row.url);
                        }
                      }
                    }, params.row.sequence)
                  ]),
                  h('div', {
                      slot: 'content'
                    },
                    [
                      h(baseChild, {
                      props: {
                        child: params.row.child
                      }
                    })
                  ])
                ])
              );
            } else {
              ret.push(
                h('div', [
                  h('a', {
                    href: 'javascript:void(0);',
                    style: {
                      marginRight: '5px'
                    },
                    on: {
                      click: () => {
                        this.jumper(params.row.url);
                      }
                    }
                  }, params.row.sequence)
                ])
              );
            }
            return h('div', ret);
          }
        },
        {
          title: '',
          key: 'title',
          align: 'center',
          minWidth: 300
        },
        {
          title: '',
          key: 'id',
          align: 'center',
          minWidth: 120,
          render: (h, params) => {
            return h('div', [
              h('p', {
              }, params.row.createdUser.name)
            ]);
          }
        },
        {
          title: 'RD',
          key: 'rd_owner',
          align: 'center',
          minWidth: 120,
          render: (h, params) => {
            return h('div', [
              h('p', {
              }, params.row.rd_owner.name)
            ]);
          }
        },
        {
          title: 'QA',
          key: 'qa_owner',
          align: 'center',
          minWidth: 120,
          render: (h, params) => {
            return h('div', [
              h('p', {
              }, params.row.qa_owner.name)
            ]);
          }
        },
        {
          title: '',
          key: 'status',
          align: 'center',
          minWidth: 80
        },
        {
          title: '',
          key: 'operation',
          align: 'center',
          fixed: 'right',
          minWidth: 130,
          render: (h, params) => {
            let ret = [];
            // 
            if (['', ''].indexOf(params.row.status) >= 0) {
              ret.push(
                h(
                  'Button',
                  {
                    props: {
                      type: 'primary',
                      size: 'small'
                    },
                    style: {
                      marginTop: '2px',
                      marginBottom: '2px',
                      marginRight: '2px'
                    },
                    on: {
                      click: () => {
                        this.setTestModa(params.row);
                      }
                    }
                  },
                  ''
                )
              );
            } else if (['', ''].indexOf(params.row.status) >= 0) {
              if (params.row.test_status !== 'running' && this.userInfo.identifyQA) {
                // 
                ret.push(
                  h(
                    'Button',
                    {
                      props: {
                        type: 'primary',
                        size: 'small'
                      },
                      style: {
                        marginTop: '2px',
                        marginBottom: '2px',
                        marginRight: '2px'
                      },
                      on: {
                        click: () => {
                          this.createTestJob(params.row);
                        }
                      }
                    },
                    ''
                  )
                );
              } else {
                ret.push(
                  h(
                    'Button',
                    {
                      props: {
                        type: 'primary',
                        size: 'small',
                        disabled: true
                      },
                      style: {
                        marginTop: '2px',
                        marginBottom: '2px',
                        marginRight: '2px'
                      }
                    },
                    ''
                  )
                );
              }
              // 
              if (params.row.test_id) {
                ret.push(
                  h(
                    'Button',
                    {
                      props: {
                        type: 'primary',
                        size: 'small'
                      },
                      style: {
                        marginTop: '2px',
                        marginBottom: '2px',
                        marginRight: '2px'
                      },
                      on: {
                        click: () => {
                          this.getReqDetail(params.row);
                        }
                      }
                    },
                    ''
                  )
                );
              } else {
                ret.push(
                  h(
                    'Button',
                    {
                      props: {
                        type: 'primary',
                        size: 'small',
                        disabled: true
                      },
                      style: {
                        marginTop: '2px',
                        marginBottom: '2px',
                        marginRight: '2px'
                      }
                    },
                    ''
                  )
                );
              }
            } else if (['', ''].indexOf(params.row.status) >= 0) {
              if (params.row.test_id) {
                ret.push(
                  h(
                    'Button',
                    {
                      props: {
                        type: 'primary',
                        size: 'small'
                      },
                      style: {
                        marginTop: '2px',
                        marginBottom: '2px',
                        marginRight: '2px'
                      },
                      on: {
                        click: () => {
                          this.getReqDetail(params.row);
                        }
                      }
                    },
                    ''
                  )
                );
              } else {
                ret.push(
                  h(
                    'Button',
                    {
                      props: {
                        type: 'primary',
                        size: 'small',
                        disabled: true
                      },
                      style: {
                        marginTop: '2px',
                        marginBottom: '2px',
                        marginRight: '2px'
                      }
                    },
                    ''
                  )
                );
              }
            } else {
              ret.push(
                h(
                  'Button',
                  {
                    props: {
                      type: 'primary',
                      size: 'small',
                      disabled: true
                    },
                    style: {
                      marginTop: '5px',
                      marginBottom: '5px',
                      marginRight: '5px'
                    }
                  },
                  ''
                )
              );
            }
            return h(
              'div',
              {
                style: {
                  'text-align': 'center'
                }
              },
              ret
            );
          }
        }
      ]
    };
  },
  watch: {
  },
  mounted: async function () {
    this.initData();
    await this.CheckUserInfo();
    await this.getData();
  },
  components: {
    TestJob
  },
  computed: {
  },
  methods: {
    async handleReset() {
      await this.$refs.child.initData();
    },
    async handleSubmit() {
      await this.$refs.child.handleSubmit();
    },
    async SubmitRequirement(jid) {
      // jidicafeidjid
      let params = {
        method: '',
        test_id: jid,
        test_status: 'running',
        icafe_id: this.selectRow.sequence,
        rd: this.selectRow.rd_owner.username,
        qa: this.selectRow.qa_owner.username,
        repo: this.selectRow.repo,
        pr: this.selectRow.pr
      };
      const { code, message } = await api.post(StartTestUrl, params);
      if (parseInt(code, 10) === 200) {
        await this.getData();
      } else {
        this.$Message.error({
          content: ': ' + message,
          duration: 30,
          closable: true
        });
      }
    },
    closeModal(params) {
      // 
      this.showModa = params;
    },
    getBeginData() {
      // end_time+1 end_time000
      let begin_time = new Date();
      begin_time = begin_time.setDate(begin_time.getDate() - 30);
      begin_time = new Date(begin_time);
      return begin_time;
    },
    async CheckUserInfo() {
      const { code, data, message } = await api.get(UserCheckUrl);
      if (parseInt(code, 10) === 200) {
        this.userInfo.identifyQA = data.identifyQA;
      } else {
        this.$Message.error({
          content: ': ' + message,
          duration: 30,
          closable: true
        });
      }
    },
    async pageChange(pageNum) {
      this.page = pageNum;
      await this.getData();
    },
    async searchByfilter() {
      // 
    },
    async searchByfilters() {
      this.page = 1;
      await this.getData();
    },
    async getData() {
      this.content = [];
      // rdrdQAQA
      if (this.userInfo.identifyQA) {
        this.search.qaname = this.userInfo.username;
      } else {
        this.search.rdname = this.userInfo.username;
      }
      let params = {
        rd: this.search.rdname,
        qa: this.search.qaname,
        keyword: this.search.keyword,
        page: this.page,
        status: this.search.status === '' ? null : this.search.status,
        page_num: this.pagesize,
        begin_time: dateFmt(this.search.dt[0], 'yyyy-MM-dd'),
        end_time: dateFmt(this.search.dt[1], 'yyyy-MM-dd')
      };
      const {code, data, message, all_count} = await api.get(RequirementSearchUrl, params);
      if (parseInt(code, 10) === 200) {
        this.total = all_count;
        this.content = data;
        for (let i = 0; i < this.content.length; i++) {
          // 
          this.content[i].child = this.content[i].children;
          this.content[i].children = null;
        }
      } else {
        this.$Message.error({
          content: ': ' + message,
          duration: 30,
          closable: true
        });
      }
    },
    initData() {
      this.selectRow = null;
      this.search = {
        keyword: '',
        qaname: '',
        rdname: '',
        status: '',
        dt: [this.getBeginData(), new Date()]
      };
      this.initReqData();
      this.initTestData();
    },
    async getTestJobDetail(item) {
      // 
      let _params = {
        jid: item.test_id
      };
      // branchcommit
      const { href } = this.$router.resolve({name: 'SingleDetail', params: _params});
      window.open(href, '_blank');
    },
    jumper(url) {
      window.open(url, '_blank');
    },
    async getReqDetail(item) {
      // 
      let _params = {
        reqid: item.sequence
      };
      // branchcommit
      const { href } = this.$router.resolve({name: 'ReqDetails', params: _params});
      window.open(href, '_blank');
    },
    async createTestJob(item) {
      this.selectRow = item;
      this.showModa = true;
    },
    // 
    async handleCreateReq() {
      this.$refs.addReqForm.validate(async (valid) => {
        if (valid) {
          await this.createJob();
          await this.searchByfilters();
        } else {
          this.$Message.error('!');
        }
      });
    },
    initReqData() {
      this.createReqModa = false;
      this.addReqForm = {
        type: 'Task',
        rd_owner: '',
        qa_owner: '',
        title: '',
        detail: ''
      };
    },
    handleResetReq() {
      this.initReqData();
    },
    async showCreateModa() {
      // 
      this.initReqData();
      if (this.userInfo.identifyQA) {
        this.addReqForm.qa_owner = this.userInfo.username;
      } else {
        this.addReqForm.rd_owner = this.userInfo.username;
      }
      this.createReqModa = true;
    },
    async createJob() {
      // API
      const { code, message } = await api.post(CreateReqUrl, this.addReqForm);
      if (parseInt(code, 10) === 200) {
        this.initReqData();
      } else {
        this.$Message.error({
          content: ': ' + message,
          duration: 30,
          closable: true
        });
        this.initReqData();
      }
    },
    // 
    setTestModa(item) {
      this.initTestData();
      this.selectRow = item;
      this.reqDetail.rd = item.rd_owner.username ? item.rd_owner.username : this.userInfo.username;
      this.reqDetail.icafe_id = item.sequence;
      this.reqDetail.qa = item.qa_owner.username;
      this.reqDetail.repo = item.repo;
      this.reqDetail.pr = item.pr;
      this.createTestModa = true;
    },
    initTestData() {
      this.createTestModa = false;
      this.selectRow = null;
      this.reqDetail = {
        method: '',
        rd: '',
        qa: '',
        pr: '',
        branch: '',
        repo: '',
        icafe_id: ''
      };
    },
    handleResetTest() {
      this.initTestData();
    },
    async handleCreateTest() {
      this.$refs.reqDetail.validate(async (valid) => {
        if (valid) {
          const { code, message } = await api.post(StartTestUrl, this.reqDetail);
          if (parseInt(code, 10) === 200) {
            this.initTestData();
            await this.getData();
          } else {
            this.$Message.error({
              content: ': ' + message,
              duration: 30,
              closable: true
            });
            this.initTestData();
          }
        } else {
          this.$Message.error('!');
        }
      });
    }
  }
};
</script>

<style scoped>
.form-css {
  margin-bottom: 12px;
}
.btn-success {
  color: #fff;
  background-color: #67c23a;
  border-color: #67c23a;
}
</style>


